.headlineButtonWrapper {
    display: inline-block;
}

.headlineButton {
    background: #fbfbfb;
    color: #888;
    font-size: 18px;
    border: 0;
    padding-top: 5px;
    vertical-align: bottom;
    height: 34px;
    width: 36px;
}

.headlineButton:hover,
.headlineButton:focus {
    background: #f3f3f3;
}
.addImage {
    background: #FFF;
    display: inline-block;
}

.addImagePopover {
    margin-top: 10px;
    background: #FFF;
    position: absolute;
    height: 54px;
    width: 300px;
    border-radius: 2px;
    padding: 10px;
    box-shadow: 0px 4px 30px 0px rgba(220,220,220,1);
    z-index: 1000;
}

.addImageClosedPopover {
    display: none;
}

.addImageButton {
    box-sizing: border-box;
    background: #fff;
    border: 1px solid #ddd;
    padding: 0;
    color: #888;
    margin: 0;
    border-radius: 1.5em;
    cursor: pointer;
    height: 1.5em;
    width: 2.5em;
    font-size: 1.5em;
    line-height: 1.2em;
    margin: 0;
}

.addImageButton:focus {
    outline: 0; /* reset for :focus */
}

.addImageButton:hover {
    background: #f3f3f3;
}

.addImageButton:active {
    background: #e6e6e6;
}

.addImagePressedButton {
    /*composes: addImageButton;*/
    box-sizing: border-box;
    /*background: #fff;*/
    border: 1px solid #ddd;
    padding: 0;
    color: #888;
    margin: 0;
    border-radius: 1.5em;
    cursor: pointer;
    height: 1.5em;
    width: 2.5em;
    font-size: 1.5em;
    line-height: 1.2em;
    /*margin: 0;*/
    background: #ededed;
}

.addImageBottomGradient {
    width: 100%;
    height: 1em;
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: white;
    pointer-events: none;
    background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
}

.addImageInput {
    box-sizing: border-box;
    border: 1px solid #ddd;
    cursor: text;
    padding: 4px;
    width: 78%;
    border-radius: 2px;
    margin-bottom: 1em;
    box-shadow: inset 0px 1px 8px -3px #ABABAB;
    background: #fefefe;
}

.addImageConfirmButton {
    box-sizing: border-box;
    background: #fff;
    border: 1px solid #ddd;
    padding: 0;
    color: #888;
    margin: 0;
    border-radius: 2.1em;
    cursor: pointer;
    height: 2.1em;
    width: 18%;
    font-size: 1em;
    line-height: 2.1em;
    margin: 0;
    margin-left: 4%;
}

.addImageConfirmButton:focus {
    outline: 0; /* reset for :focus */
}

.addImageConfirmButton:hover {
    background: #f3f3f3;
}

.addImageConfirmButton:active {
    background: #e6e6e6;
}

.draftTextEditor {
    background-color: white;
    border: 1px solid;
}
.editor {
    box-sizing: border-box;
    border: 1px solid #ddd;
    cursor: text;
    padding: 16px;
    border-radius: 2px;
    margin-bottom: 2em;
    box-shadow: inset 0px 1px 8px -3px #ABABAB;
    background: #fefefe;
}

.editor .public-DraftEditor-content {
    min-height: 140px;
}
.options {
    margin-bottom: 10px;
    margin-top: 10px;
}
.input {
    height: 34px;
    width: 220px;
    padding: 0 12px;
    font-size: 15px;
    font-family: inherit;
    background-color: transparent;
    border: none;
    color: #ddd;
}

.input:focus {
    outline: none;
}

.input::-webkit-input-placeholder {
    color: #aaa;
}

.input::-ms-input-placeholder {
    color: #aaa;
}

.input::placeholder {
    color: #aaa;
}

.inputInvalid {
    color: #e65757;
}

.link {
    color: #2996da;
    text-decoration: underline;
}
.alignmentTool {
    left: 50%;
    -webkit-transform: translate(-50%) scale(0);
            transform: translate(-50%) scale(0);
    position: absolute;
    border: 1px solid #111;
    background: #333;
    border-radius: 4px;
    box-shadow: 0px 1px 3px 0px rgba(220, 220, 220, 1);
    z-index: 2;
    box-sizing: border-box;
}

.alignmentTool:after,
.alignmentTool:before {
    top: 100%;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.alignmentTool:after {
    border-color: rgba(255, 255, 255, 0);
    border-top-color: #333;
    border-width: 4px;
    margin-left: -4px;
}

.alignmentTool:before {
    border-color: rgba(221, 221, 221, 0);
    border-top-color: #111;
    border-width: 6px;
    margin-left: -6px;
}
.buttonWrapper {
    display: inline-block;
}

.button {
    background: #333;
    color: #ddd;
    font-size: 18px;
    border: 0;
    padding-top: 5px;
    vertical-align: bottom;
    height: 34px;
    width: 36px;
    border-radius: 4px;
}

.button svg {
    fill: #ddd;
}

.button:hover,
.button:focus {
    background: #444;
    outline: 0; /* reset for :focus */
}

.active {
    color: #6a9cc9;
}

.active svg {
    fill: #6a9cc9;
}
.draftJsEditor p {
    text-align: justify;
    font-size: 20px;
}
.draftJsEditor div:first-child {
    text-align: center;
}
.draftJsEditor image {
    display: block;margin: auto;
}
